.autocomplete-concierge {
    @include pnp-svg-icon-classes;
    @include pnp-media-breakpoint-up(md) {
        width: 700px;
        margin: 0 50px;
    }

    &__form-wrapper {
        display: flex;
        border: solid 1px $primary-a;
    }

    &__input {
        @include pnp-typeface(text-16, no-margin);
        display: block;
        width: 100%;
        min-width: 288px;
        padding: 8px 42px 8px 8px;
        line-height: 30px;
        border: 0;
        outline: 0;
        @include pnp-media-breakpoint-up(sm) {
            @include pnp-typeface(text-18, no-margin);
            min-width: 192px;
            line-height: 30px;
        }
    }

    &__submit {
        @include pnp-reset-button;
        display: inline-flex;
        padding: 0.75rem 1rem;
        color: $primary-a;
        background-color: $white;

        cursor: pointer;
        transition: background-color 0.25s ease, color 0.25s ease;

        &:hover,
        &:focus {
            color: $white;
            background-color: $primary-a;
            outline-offset: -2px;
        }
        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    &__results {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        min-width: 280px;
        background: $white;
        outline: none;
        box-shadow: 0 20px 50px 0 rgba(0, 0, 0, 0.15);
    }

    &__section {
        flex: 1 1 100%;
        padding: 0 22px 22px;

        @include pnp-media-breakpoint-up(md) {
            flex: 1 1 40%;

            &:first-child {
                flex: 1 1 100%;
                border-bottom: solid 1px $grey-20;
            }

            &:nth-child(2) {
                margin-right: 22px;
            }
        }

        ul {
            @include pnp-list-reset();
        }
    }

    &__section-title {
        @include pnp-typeface(text-16-bold, no-margin);
        color: $primary-a;
        text-transform: uppercase;
    }

    &__search-result-item {
        @include pnp-svg-icon-classes;
        @include pnp-typeface(text-14, no-margin);
        margin: 0 0 28px;
        color: $black;
        cursor: default;

        &:hover,
        &:focus,
        &[aria-selected='true'] {
            background: $primary-a-light;
        }

        &.organic {
            @include pnp-typeface(text-16, no-margin);
            margin: 0;
        }

        .highlight {
            @include pnp-typeface(text-18-bold, no-margin);
        }
    }

    .title {
        @include pnp-reset-heading();
        @include pnp-typeface(text-16-bold);
        color: $black;
    }

    .autocomplete-results__metadata {
        display: flex;
        align-items: flex-start;

        .svg-icon {
            flex-shrink: 0;
            margin-right: 10px;
            transform: scale(0.8);
        }
    }
}
